import React, { useState, useEffect } from 'react';
import Box from 'ui-box';

const backgroundColor = '#f7f7f7';
const fontColor = '#757575'; //'#d9d9d9'; //'#e5e5e5'; //'#f2f2f2';

const App = () => {
  const [textAreaHeight, setTextAreaHeight] = useState('48px');

  const resize = (event) => {
    // https://stackoverflow.com/questions/10722058/height-of-textarea-increases-when-value-increased-but-does-not-reduce-when-value
    if (event.target) {
      event.target.style.height = '1px';
      setTextAreaHeight(event.target.scrollHeight + 'px');
      event.target.style.height = textAreaHeight;
    }
  };
  return (
    <Box
      backgroundColor={backgroundColor}
      width="100%"
      height="100%"
      overflow="hidden"
    >
      <Box
        as={'nav'}
        height={64}
        width="100%"
        display="flex"
        alignItems="center"
        paddingLeft={24}
      >
        <Box color={fontColor} fontFamily="Alatsi">
          OmOu
        </Box>
      </Box>
      <Box position={'relative'} left={'20vw'} top={40}>
        <Box display="flex">
          <textarea
            style={{
              backgroundColor: backgroundColor,
              color: fontColor,
              fontFamily: 'Alatsi',
              fontSize: '34px',
              border: 'none',
              borderLeft: '4px solid ' + fontColor,
              paddingLeft: '12px',
              outline: 'none',
              resize: 'none',
              height: textAreaHeight,
            }}
            rows="1"
            type="text"
            placeholder="What do you think?"
            onChange={resize}
            onKeyDown={resize}
            onPaste={resize}
            onCut={resize}
            onDrop={resize}
          ></textarea>
        </Box>
      </Box>
    </Box>
  );
};

export default App;
